.game {
    display: grid;
    width: 100%;
    grid-template-columns: 3fr 1fr;
}

.question-panel {
    background-color: #F5F5F7;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.question-panel__header {
    font-size: 2rem;
}

.question-panel__answers {
    width: 90%;
    display: grid;
    grid-row-gap: 32px;
    grid-template-columns: repeat(2, 1fr);
}

.level-panel {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.level-panel__money {
    width: 100%;
    margin-bottom: 8px;
    font-size: 1.2rem;
}

.level-panel__money-unactive {
    color: #D0D0D8;
}

.level-panel__money-current {
    color: #FF8B37;
}

.level-panel__money-current .border {
    stroke: #FF8B37;
}

.level-panel__money {
    width: 100%;
    margin-bottom: 8px;
    font-size: 1.2rem;
}

.level-panel__money .border-item {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.question-panel__answers .border:hover {
    stroke: #FF8B37;
    transition: stroke 0.1s;
}

.border-button-selected .border {
    stroke: #FF8B37;
    fill: #FFF3EB;
    transition: fill 0.2s;
}

.border-button-wrong .border {
    stroke: #EC6259;
    fill: #FDEEED;
    transition: fill 0.2s;
}

.border-button-correct .border {
    stroke: #47D867;
    fill: #E6FAEA;
    transition: fill 0.2s;
}

.answer__caption__letter {
    font-weight: 600;
    color: #FF8B37;
    margin-right: 8px;
}

@media screen and (max-width: 1250px) {
    .question-panel__answers {
        grid-row-gap: 16px;
        grid-template-columns: 1fr;
    }

    .game {
        grid-template-columns: 2fr 1fr;
    }
}